.title {
    text-align: center;
    margin-top: 35px;
    margin-bottom: 35px;
    font-size: 45px;
}

.appear {
    opacity: 0;
    transform: translate(-100%);
}

.appearActive {
    opacity: 1;
    transform: translate(0);
    transition: all 500ms linear;
}

.nothing {
    text-align: center;
    font-size: 35px;
    margin-top: 50px;
}